import { Component, OnInit, Input } from '@angular/core';

import { CAREER_QUESTIONS } from '../../mock/Questions';
import { NetworkService } from './../../../services/network.service';
import { PEOPLE } from './../../mock/People';
import { THIS_EXPR } from '@angular/compiler/src/output/output_ast';

@Component({
  selector: 'app-table',
  templateUrl: './table.component.html',
  styleUrls: ['./table.component.css']
})
export class TableComponent implements OnInit {
  @Input() title: string;
  displayColumns = [];
  networkNames = [];
  questions = [];
  constructor(private network: NetworkService) { }

  ngOnInit() {
    this.networkNames = PEOPLE.map(person => person.name);
    this.buildColumns(this.title, this.networkNames);
    // TODO: Refactor the below code so that the questions are passed into the component
    // as an input when psychological help questions are determined
    this.questions = CAREER_QUESTIONS;
  }

  buildColumns(tableName, members): void {
    this.displayColumns.push(tableName, ...members);
  }

}
