import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { ProfileComponent } from './components/profile/profile.component';
import { AuthGuard } from './auth.guard';
import { LandingComponent } from './components/landing/landing.component';
import { NetworkBuildComponent } from './components/network-build/network-build.component';
import { SurveyStartComponent } from './components/survey-start/survey-start.component';
import { UserHomeComponent } from './components/user-home/user-home.component';
import { ValueIntroComponent } from './components/value-intro/value-intro.component';
import { GoalsComponent } from './components/goals/goals.component';
import { ValueComponent } from './components/value/value.component';

const routes: Routes = [
  {
    path: '',
    component: LandingComponent,
    pathMatch: 'full'
  },
  {
    path: 'goals',
    component: GoalsComponent,
    canActivate: [AuthGuard]
  },
  {
    path: 'network-build',
    component: NetworkBuildComponent,
    canActivate: [AuthGuard]
  },
  {
    path: 'profile',
    component: ProfileComponent,
    canActivate: [AuthGuard]
  },
  {
    path: 'survey-start',
    component: SurveyStartComponent,
    canActivate: [AuthGuard]
  },
  {
    path: 'user-home',
    component: UserHomeComponent,
    canActivate: [AuthGuard]
  },
  {
    path: 'value',
    component: ValueComponent,
    canActivate: [AuthGuard]
  },
  {
    path: 'value-intro',
    component: ValueIntroComponent,
    canActivate: [AuthGuard]
  }
];
@NgModule({
  imports: [RouterModule.forRoot(routes)],

  exports: [RouterModule]
})
export class AppRoutingModule {}
